.App {
  text-align: left;
}

.App-header {
  background-color: white;
}

.App-logo {
  height: 110px;
  display: block;
}

.App-body {
  background: linear-gradient(0deg,#fff 2.58%,#f5f6fa 51.23%);
  padding: 20px 120px;
}

.main-container {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
}

.main-container > div {
  padding: 5px 15px;
}

.main-btn {
  padding: 10px;
  background-color: #3796f6;
  border: none;
  color: white;
}